import * as React from 'react';
import {
  widget,
  ChartingLibraryWidgetOptions,
  LanguageCode,
  IChartingLibraryWidget,
} from '../../charting_library/charting_library.min';

export interface IChartContainerProps {
  symbol: ChartingLibraryWidgetOptions['symbol'];
  interval: ChartingLibraryWidgetOptions['interval'];

  // BEWARE: no trailing slash is expected in feed URL
  datafeedUrl: string;
  libraryPath: ChartingLibraryWidgetOptions['library_path'];
  chartsStorageUrl: ChartingLibraryWidgetOptions['charts_storage_url'];
  chartsStorageApiVersion: ChartingLibraryWidgetOptions['charts_storage_api_version'];
  clientId: ChartingLibraryWidgetOptions['client_id'];
  userId: ChartingLibraryWidgetOptions['user_id'];
  fullscreen: ChartingLibraryWidgetOptions['fullscreen'];
  autosize: ChartingLibraryWidgetOptions['autosize'];
  studiesOverrides: ChartingLibraryWidgetOptions['studies_overrides'];
  containerId: ChartingLibraryWidgetOptions['container_id'];
  type: string;
}

// export interface ChartContainerState {
// }

function getLanguageFromURL(): LanguageCode | null {
  const regex = new RegExp('[\\?&]lang=([^&#]*)');
  const results = regex.exec(location.search);
  return results === null
    ? null
    : (decodeURIComponent(results[1].replace(/\+/g, ' ')) as LanguageCode);
}

class TVChart extends React.PureComponent<Partial<IChartContainerProps>> {
  public static defaultProps: IChartContainerProps = {
    symbol: 'AAPL',
    interval: 'D',
    containerId: 'tv_chart_container',
    datafeedUrl: 'https://demo_feed.tradingview.com',
    libraryPath: '/charting_library/',
    chartsStorageUrl: 'https://saveload.tradingview.com',
    chartsStorageApiVersion: '1.1',
    clientId: 'tradingview.com',
    userId: 'public_user_id',
    fullscreen: false,
    autosize: true,
    studiesOverrides: {},
    type: 'advanced',
  };

  private tvWidget: IChartingLibraryWidget | null = null;

  public componentDidMount(): void {
    const widgetOptions: ChartingLibraryWidgetOptions = {
      symbol: this.props.symbol as string,
      // BEWARE: no trailing slash is expected in feed URL
      // tslint:disable-next-line:no-any
      datafeed: new (window as any).Datafeeds.UDFCompatibleDatafeed(
        this.props.datafeedUrl,
      ),
      interval: this.props.interval as ChartingLibraryWidgetOptions['interval'],
      container_id: this.props
        .containerId as ChartingLibraryWidgetOptions['container_id'],
      library_path: this.props.libraryPath as string,
      locale: getLanguageFromURL() || 'en',
      disabled_features: [
        'use_localstorage_for_settings',
        'header_symbol_search',
        'header_undo_redo',
        'volume_force_overlay',
        'border_around_the_chart',
        'display_market_status',
        'edit_buttons_in_legend',
      ],
      enabled_features: ['study_templates'],
      charts_storage_url: this.props.chartsStorageUrl,
      charts_storage_api_version: this.props.chartsStorageApiVersion,
      client_id: this.props.clientId,
      user_id: this.props.userId,
      fullscreen: this.props.fullscreen,
      autosize: this.props.autosize,
      studies_overrides: this.props.studiesOverrides,
      overrides: {
        'mainSeriesProperties.style': 3,
        'mainSeriesProperties.priceLineWidth': 1.5,
        'mainSeriesProperties.priceLineColor': '#118CDA',
        'mainSeriesProperties.areaStyle.color1': '#d6f4ff',
        'mainSeriesProperties.areaStyle.color2': '#ffffff',
        'mainSeriesProperties.areaStyle.linecolor': '#0EAFFF',
        'mainSeriesProperties.areaStyle.linewidth': 1.2,
        'paneProperties.crossHairProperties.style': 0,
        'scalesProperties.textColor': '#79899A',
        volumePaneSize: 'medium',
      },
    };

    const regularDisabledOptions = [
      'edit_buttons_in_legend',
      'left_toolbar',
      'header_widget_dom_node',
      'legend_context_menu',
      'pane_context_menu',
      'timeframes_toolbar',
      'create_volume_indicator_by_default',
      'control_bar',
      'chart_scroll',
      'chart_zoom',
      'property_pages',
    ];

    const regularOverrides = {
      'paneProperties.legendProperties.showStudyArguments': false,
      'paneProperties.legendProperties.showStudyTitles': false,
      'paneProperties.legendProperties.showStudyValues': false,
      'paneProperties.legendProperties.showSeriesTitle': false,
      'paneProperties.legendProperties.showSeriesOHLC': false,
      'paneProperties.legendProperties.showLegend': false,
      'scalesProperties.fontSize': 12,
      'scalesProperties.lineColor': '#ffffff',
    };

    const simpleOverrides = {
      'scalesProperties.showRightScale': false,
      'mainSeriesProperties.showPriceLine': false,
      'paneProperties.horzGridProperties.color': '#ffffff',
      'paneProperties.vertGridProperties.color': '#ffffff',
    };

    if (this.props.type !== 'advanced') {
      if (widgetOptions.disabled_features) {
        widgetOptions.disabled_features.push(...regularDisabledOptions);
      }
      if (this.props.type === 'regular') {
        widgetOptions.overrides = {
          ...widgetOptions.overrides,
          ...regularOverrides,
        };
      } else if (this.props.type === 'simple') {
        widgetOptions.overrides = {
          ...widgetOptions.overrides,
          ...regularOverrides,
          ...simpleOverrides,
        };
      }
    }

    const tvWidget = new widget(widgetOptions);
    this.tvWidget = tvWidget;
  }

  public componentWillUnmount(): void {
    if (this.tvWidget !== null) {
      this.tvWidget.remove();
      this.tvWidget = null;
    }
  }

  public render(): JSX.Element {
    return (
      <div
        className={
          'tv-chart-container ' +
          (this.props.type !== 'advanced' ? 'tv-chart-container--custom' : '')
        }
      >
        <div
          id={this.props.containerId}
          className={
            'tv-chart ' +
            (this.props.type !== 'advanced' ? 'tv-chart--custom' : '')
          }
        />
      </div>
    );
  }
}

export default TVChart;



