import * as React from 'react';
import { AppLayout as Layout } from './layouts/Layout';
import TopBanner from './components/TopBanner/TopBanner';
import MenuHeader from './components/MenuHeader/MenuHeader';
import MenuNavigation from './components/MenuNavigation/MenuNavigation';
import RoutesContainer from './containers/RoutesContainer/RoutesContainer';
import { ILayoutComponent } from './types/global';

class App extends React.Component {
  public generateLayout = (data: ILayoutComponent) => {
    switch (data.component) {
      case 'TopBanner':
        return <TopBanner {...data} />;
      case 'MenuHeader':
        return <MenuHeader {...data} />;
      case 'MenuNavigation':
        return <MenuNavigation {...data} />;
      case 'RoutesContainer':
        return <RoutesContainer {...data} />;
      default:
        return null;
    }
  };

  public render() {
    return (
      <div className="app-container">
        {Layout.map(data => (
          <React.Fragment key={data.id}>
            {this.generateLayout(data)}
          </React.Fragment>
        ))}
      </div>
    );
  }
}

export default App;



