import * as React from 'react';
import Banner from '../../assets/banners/side-banner.png';
import { ILayoutComponent } from '../../types/global';
import { Responsive } from 'semantic-ui-react';

const SideBanner = (props: ILayoutComponent) => {
  return (
    <Responsive minWidth={1440} className="side-banner">
      <img src={Banner} alt={props.properties.alt} />
    </Responsive>
  );
};

export default SideBanner;



