import * as React from 'react';
import Markets from '../Markets/Markets';
import SideBanner from '../../components/SideBanner/SideBanner';
import { Switch, Route } from 'react-router-dom';
import { ILayoutComponent } from '../../types/global';

class RoutesContainer extends React.Component<ILayoutComponent> {
  public render() {
    return (
      <Switch>
        <main className="page-container">
          <aside>
            {(this.props.content as ILayoutComponent[]).map(
              (data, index) =>
                index === 0 ? <SideBanner key={data.id} {...data} /> : null,
            )}
          </aside>

          <Route path="/markets/:tab" component={Markets} />

          <aside>
            {(this.props.content as ILayoutComponent[]).map(
              (data, index) =>
                index === 1 ? <SideBanner key={data.id} {...data} /> : null,
            )}
          </aside>
        </main>
      </Switch>
    );
  }
}

export default RoutesContainer;



