import * as React from 'react';
import { ILayoutComponent } from '../../types/global';
import GenericTable from '../GenericTable/GenericTable';
import TVChart from '../TVChart/TVChart';
import TabsMenu from '../TabsMenu/TabsMenu';
import { Grid, Icon } from 'semantic-ui-react';
import { PartnerBanner } from '../PartnerBanner/PartnerBanner';

export interface IState {
  title: string;
  subtitle: string;
  arrow: boolean;
  data: any[];
}

class GenericWrapper extends React.Component<ILayoutComponent, IState> {
  public state: IState = {
    title: '',
    subtitle: '',
    arrow: false,
    data: [],
  };

  public fetchData = (url?: string) => {
    if (url !== undefined) {
      this.setState({
        title: '',
        subtitle: '',
        arrow: false,
        data: [],
      });
      fetch(url)
        .then(res => res.json())
        .then(data => this.setState({ ...data }))
        .catch(error => console.error(error));
    }
  };

  public componentDidMount() {
    this.fetchData(this.props.properties.url);
  }

  public componentDidUpdate(prevProps: any) {
    if (prevProps.properties.url !== this.props.properties.url) {
      this.fetchData(this.props.properties.url);
    }
  }

  public generateComponent(content: ILayoutComponent) {
    const { component, id } = content;
    switch (component) {
      case 'GenericTable':
        return (
          <React.Fragment key={id}>
            {this.state.data.map(data => (
              <Grid.Column key={data.id}>
                <GenericTable data={data} />
              </Grid.Column>
            ))}
          </React.Fragment>
        );
      case 'TVChart':
        return (
          <React.Fragment key={id}>
            {this.state.data.map(data => (
              <Grid.Column key={data.id}>
                <TVChart type="regular" />
              </Grid.Column>
            ))}
          </React.Fragment>
        );
      case 'AdvancedGraph':
        return (
          <Grid.Column key={id}>
            <TVChart type="advanced" />
          </Grid.Column>
        );
      case 'TabsMenu':
        return (
          <Grid.Column width={16} key={id}>
            <TabsMenu {...content} />
          </Grid.Column>
        );
      case 'PartnerBanner':
        return <PartnerBanner />;
      default:
        return (
          <Grid.Column key={id}>
            <div className="greyBox" />
          </Grid.Column>
        );
    }
  }

  public render() {
    return (
      <Grid.Row className="generic-wrapper">
        {this.state.title && (
          <Grid.Column width={16}>
            <div className="generic-wrapper__header">
              <h3 className="generic-wrapper__header__title">
                {this.state.title}
                {this.state.arrow ? <Icon name="triangle right" /> : ''}
              </h3>
              <span className="generic-wrapper__header__subtitle">
                {this.state.subtitle}
              </span>
            </div>
          </Grid.Column>
        )}

        {(this.props.content as ILayoutComponent[]).map(content =>
          this.generateComponent(content),
        )}

        {this.props.component !== 'Banner' ? (
          <Grid.Column width={16}>
            <div className="generic-wrapper__divider" />
          </Grid.Column>
        ) : null}
      </Grid.Row>
    );
  }
}

export default GenericWrapper;



