import * as React from 'react';
import { Dropdown, Responsive } from 'semantic-ui-react';
import { ILayoutComponent } from '../../types/global';
import { media } from '../../helpers/BreakPoints';

export interface IProps extends ILayoutComponent {
  history?: any;
}

class TabsMenu extends React.Component<IProps> {
  public state = {
    activeTab: this.props.content[0].properties.title,
  };

  public handleTabClick = (title: any) => {
    if (this.props.history) {
      this.props.history.push(
        `/markets/${(title as string).toLowerCase().replace(' ', '')}`,
      );
    }
    this.setState({ activeTab: title });
  };

  public render() {
    const { activeTab } = this.state;

    const createTab = (className: string, title: any) => {
      return (
        <button
          className={className}
          onClick={() => this.handleTabClick(title)}
        >
          <p className="tab-menu__tab__text">{title}</p>
        </button>
      );
    };

    return (
      <div className="tab-menu">
        {(this.props.content as ILayoutComponent[]).map((data, index) => {
          const title = data.properties.title;
          let className = 'tab-menu__tab ';
          className += activeTab === title ? 'tab-menu__tab--active' : '';

          return (
            <React.Fragment key={data.id}>
              <Responsive as={React.Fragment} maxWidth={media.large}>
                {index <= 1 ? createTab(className, title) : null}
              </Responsive>
              <Responsive as={React.Fragment} minWidth={media.large}>
                {createTab(className, title)}
              </Responsive>
            </React.Fragment>
          );
        })}

        <Responsive as={React.Fragment} maxWidth={media.large}>
          <button className="tab-menu__tab tab-menu__tab--dropdown">
            <Dropdown item={true} className="ei-dropdown" direction="left">
              <Dropdown.Menu className="ei-dropdown__menu">
                {(this.props.content as ILayoutComponent[]).map(
                  (data, index) => {
                    const titles = data.properties.title;

                    return index > 1 ? (
                      <Dropdown.Item
                        key={data.id}
                        className="ei-dropdown__menu__item"
                        onClick={() => this.handleTabClick(titles)}
                      >
                        {titles}
                      </Dropdown.Item>
                    ) : null;
                  },
                )}
              </Dropdown.Menu>
            </Dropdown>
          </button>
        </Responsive>
      </div>
    );
  }
}

export default TabsMenu;



