import * as React from 'react';
import Homepage from '../../assets/icons/Homepage.svg';
import { ILayoutComponent, ILayoutElement } from '../../types/global';
import { Responsive } from 'semantic-ui-react';

const MenuNavigation = (props: ILayoutComponent) => {
  return (
    <Responsive as="nav" className="nav-container" minWidth={768}>
      <div className="nav">
        <ul className="nav__list">
          {(props.content as ILayoutElement[]).map((data, index) => {
            return index <= 6 ? (
              <li key={data.id} className="nav__list__button">
                {data.properties.title !== 'Home' ? (
                  <React.Fragment>{data.properties.title}</React.Fragment>
                ) : (
                  <img src={Homepage} alt="" />
                )}
              </li>
            ) : null;
          })}
        </ul>
        <ul className="nav__list">
          {(props.content as ILayoutElement[]).map((data, index) => {
            return index > 6 ? (
              <li
                key={data.id}
                className={
                  'nav__list__button ' +
                  (data.id === 'mn-nv-webshop'
                    ? 'nav__list__button--marked'
                    : '')
                }
              >
                {data.properties.title}
              </li>
            ) : null;
          })}
        </ul>
      </div>
    </Responsive>
  );
};

export default MenuNavigation;



