import * as React from 'react';
import Degiro from '../../assets/banners/degiro.png';
import Nordnet from '../../assets/banners/nordnet.png';

export const PartnerBanner = () => {
  return (
    <div className="partner-banner">
      <a className="partner-banner__image" href="#">
        <img className="" src={Nordnet} alt="" />
      </a>
      <a className="partner-banner__image" href="#">
        <img className="" src={Degiro} alt="" />
      </a>
    </div>
  );
};



