import * as React from 'react';
import IndicatorGraph from '../IndicatorGraph/IndicatorGraph';
import { ILayoutComponent } from '../../types/global';

const TopGraphs = (props: ILayoutComponent) => {
  return (
    <section className="top-graphs">
      {(props.content as ILayoutComponent[]).map((data, index) => (
        <IndicatorGraph key={index} {...data} />
      ))}
    </section>
  );
};

export default TopGraphs;



