import 'semantic-ui-css/semantic.min.css';
import './styles/index.css';

import * as React from 'react';
import * as ReactDOM from 'react-dom';
import { Frontload } from 'react-frontload';
import * as Loadable from 'react-loadable';
import { Provider } from 'react-redux';
import { ConnectedRouter } from 'react-router-redux';
import App from './App';
import { config } from './config';
import { configStore } from './state/store';
import { IStoreConfig } from './types/global';
import registerServiceWorker from './registerServiceWorker';

const root = document.getElementById('root') as HTMLElement;
const appState: IStoreConfig = configStore();

const Application: JSX.Element = (
  <Provider store={appState.store}>
    <ConnectedRouter history={appState.history}>
      <Frontload noServerRender={true}>
        <App />
      </Frontload>
    </ConnectedRouter>
  </Provider>
);

if (config.ENV === 'production') {
  // If we're running in production, we use hydrate to get fast page loads by just
  // attaching event listeners after the initial render
  Loadable.preloadReady().then(() => ReactDOM.hydrate(Application, root));
} else {
  // If we're not running on the server, just render like normal
  ReactDOM.render(Application, root);
  registerServiceWorker();
}



