import * as React from 'react';
import { ILayoutComponent } from '../../types/global';

class StockHeader extends React.Component<ILayoutComponent> {
  public state = {
    name: 'Apple Inc (AAPL:US)',
    value: 207.99,
    change: '+0.60 (+0.29%)',
    worktime: 'Market is open until 17:00 EDT.',
  };

  public componentDidMount() {
    this.fetchData();
  }

  public render() {
    const { name, value, change, worktime } = this.state;

    return (
      <div className="stock-header">
        <React.Fragment>
          <h1 className="stock-header__title" onClick={() => this.fetchData()}>
            {name}
          </h1>
          <p className="stock-header__price">
            {value}
            <span className="stock-header__change">{change}</span>
          </p>
          <p className="stock-header__subtitle">{worktime}</p>
        </React.Fragment>
      </div>
    );
  }

  public fetchData() {
    fetch(this.props.properties.url)
      .then(res => res.json())
      .then(data => this.setState({ ...data }))
      .catch(error => console.error(error));
  }
}

export default StockHeader;



