import * as React from 'react';
import Banner from '../../assets/banners/top-banner.png';
import { ILayoutComponent } from '../../types/global';
import { Responsive } from 'semantic-ui-react';

const TopBanner = (props: ILayoutComponent) => {
  return (
    <Responsive {...Responsive.onlyComputer} className="top-banner">
      <img src={Banner} alt={props.properties.alt} />
    </Responsive>
  );
};

export default TopBanner;



