import * as React from 'react';
import TopGraphs from '../../components/TopGraphs/TopGraphs';
import StockHeader from '../../components/StockHeader/StockHeader';
import TabsMenu from '../../components/TabsMenu/TabsMenu';
import TabsGridContainer from '../TabsGridContainer/TabsGridContainer';
import { ILayoutComponent } from '../../types/global';
import { MarketsPageLayout as Layout } from '../../layouts/Layout';

const Markets = (props: any) => {
  const generateLayout = (data: ILayoutComponent) => {
    switch (data.component) {
      case 'TopGraphs':
        return <TopGraphs {...data} />;
      case 'StockHeader':
        return <StockHeader {...data} />;
      case 'TabsMenu':
        return <TabsMenu {...data} history={props.history} />;
      case 'TabsGridContainer':
        return <TabsGridContainer match={props.match} />;
      default:
        return null;
    }
  };

  return (
    <section className="content-container">
      {Layout.map(data => (
        <React.Fragment key={data.id}>{generateLayout(data)}</React.Fragment>
      ))}
    </section>
  );
};

export default Markets;



