import * as React from 'react';
import { ILayoutComponent } from '../../types/global';

class IndicatorGraph extends React.Component<ILayoutComponent> {
  public state = {
    name: 'Bava',
    value: '22,324.33',
    change: 64.0,
    changePercent: 0.3,
  };

  public componentDidMount() {
    this.fetchData();
  }

  public render() {
    const { name, value, change, changePercent } = this.state;
    const positive = change >= 0;
    const symbol = positive ? '+' : '';

    return (
      <div>
        <p className="indicator-graph__title">{name}</p>
        <p className="indicator-graph__price">{value}</p>
        <p
          className={
            'indicator-graph__change ' +
            (positive
              ? 'indicator-graph__change--positive'
              : 'indicator-graph__change--negative')
          }
        >
          {symbol + change.toFixed(2)} ({symbol + changePercent.toFixed(2)}
          %)
        </p>
      </div>
    );
  }

  private fetchData() {
    fetch(this.props.properties.url)
      .then(res => res.json())
      .then(data => this.setState({ ...data }))
      .catch(error => console.error(error));
  }
}

export default IndicatorGraph;



