import { createBrowserHistory, createMemoryHistory, History } from 'history';
import { routerMiddleware } from 'react-router-redux';
import { applyMiddleware, compose, createStore, Dispatch, Store } from 'redux';
import createSagaMiddleware, { SagaMiddleware } from 'redux-saga';
import { IStoreConfig } from '../types/global';
import { reducers } from './reducers';
import { rootSaga } from './sagas';

export const isServer: boolean = !(
  typeof window !== 'undefined' &&
  window.document &&
  window.document.createElement
);

if (isServer) {
  (global as any).window = {
    location: {},
  };
}

export function configStore(url: string = '/'): IStoreConfig {
  let composeEnhancers: any;
  let enhancers: any;
  let store: Store<any>;
  let dispatch: Dispatch<any>;
  let sagaMiddleware: SagaMiddleware<any>;

  const middleware: any = [];
  const initialHistoryOptions = { initialEntries: [url] };
  const initialState = !isServer ? window.__PRELOADED_STATE__ : {};

  const history: History = isServer
    ? createMemoryHistory(initialHistoryOptions)
    : createBrowserHistory();

  middleware.push(routerMiddleware(history));

  sagaMiddleware = createSagaMiddleware();
  middleware.push(sagaMiddleware);
  composeEnhancers = window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ || compose;
  enhancers = composeEnhancers(applyMiddleware(...middleware));
  store = createStore(reducers, initialState, enhancers);
  dispatch = store.dispatch;

  if (!isServer) {
    delete window.__PRELOADED_STATE__;
  }

  sagaMiddleware.run(rootSaga);

  return {
    dispatch,
    history,
    store,
  };
}



