import * as React from 'react';
import { Input, Responsive } from 'semantic-ui-react';
import Logo from '../../assets/logo.png';
import User from '../../assets/icons/user.png';
import Search from '../../assets/icons/search.png';
import { ILayoutComponent } from '../../types/global';

const MenuHeader = (props: ILayoutComponent) => {
  return (
    <header className="header-container">
      <div className="header">
        <img src={Logo} alt="" className="header__logo" />
        <div className="header__tools">
          <Responsive as={React.Fragment} minWidth={768}>
            <Input
              icon="search"
              size="small"
              className="header__tools__search"
              placeholder="Search"
            />

            <div className="header__tools__user">
              <img src={User} alt="" className="header__tools__user-icon" />

              <p className="header__tools__user-name">Travis_434</p>
            </div>
          </Responsive>
          <Responsive as={React.Fragment} maxWidth={767}>
            <img src={Search} alt="" className="header__tools__search-icon" />
            <div className="header__tools__hamburger">
              <div />
              <div />
              <div />
            </div>
          </Responsive>
        </div>
      </div>
    </header>
  );
};

export default MenuHeader;



