import * as React from 'react';
import { ITableData } from '../../types/global';
import { Icon } from 'semantic-ui-react';

const handleClass = (key: string, value: string) => {
  switch (key) {
    case 'change':
      switch (value[0]) {
        case '+':
          return 'generic-table__cell--success';
        default:
          return 'generic-table__cell--danger';
      }
    case 'boldColumn':
      return 'generic-table__cell--bold';
    case 'article':
      return 'left';
    case 'time':
      return 'generic-table__cell--grey';
    case 'up':
      return 'generic-table__cell--success';
    case 'down':
      return 'generic-table__cell--danger';
    case 'date':
      return 'right';
    default:
      return '';
  }
};

const generateLine = (key: string, value: string) => {
  const divider = 5.65;
  if (key === 'bid' || key === 'supply') {
    return (
      <div className="generic-table__cell__line">
        <div
          className={
            'generic-table__cell__line ' +
            (key === 'bid'
              ? 'generic-table__cell__line--success'
              : 'generic-table__cell__line--danger')
          }
          style={{ width: Number(value) / divider + '%' }}
        />
      </div>
    );
  }
  return;
};

const GenericTable = (props: ITableData) => (
  <React.Fragment>
    {props.data && (
      <table className="generic-table">
        <thead className="generic-table__header">
          <tr>
            {props.data.theads.map((data, index) => (
              <th className="generic-table__cell" key={index}>
                {data}
              </th>
            ))}
          </tr>
        </thead>
        <tbody>
          {props.data.rows.map((data, i) => (
            <tr key={i} className="generic-table__row">
              {Object.keys(data).map((key, index) => (
                <td
                  key={index}
                  className={
                    'generic-table__cell ' + handleClass(key, data[key])
                  }
                >
                  {key === 'info' ? (
                    <Icon
                      name="info circle"
                      className="generic-table__cell--grey"
                    />
                  ) : (
                    data[key]
                  )}
                  {generateLine(key, data[key])}
                </td>
              ))}
            </tr>
          ))}
        </tbody>
      </table>
    )}
  </React.Fragment>
);

export default GenericTable;



