import * as React from 'react';
import { Grid, Responsive } from 'semantic-ui-react';
import { media } from '../../helpers/BreakPoints';
import GenericWrapper from '../../components/GenericWrapper/GenericWrapper';
import { NewsTab } from '../../layouts/NewsTab';
import { AdvancedGraphTab } from '../../layouts/AdvancedGraphTab';
import { SummaryTab } from '../../layouts/SummaryTab';

const layoutList = {
  summary: SummaryTab,
  advancedgraph: AdvancedGraphTab,
  news: NewsTab,
};

class TabsGridContainer extends React.Component<any> {
  public state = {
    layout: layoutList[this.props.match.params.tab],
  };

  public componentDidUpdate(prevProps: any) {
    if (this.props.match.params.tab !== prevProps.match.params.tab) {
      this.setState({
        layout: layoutList[this.props.match.params.tab],
      });
    }
  }

  public render() {
    const generateGrid = (layout: any, order: string[]) => (
      <React.Fragment>
        {order.map((data: string, index) => (
          <Grid
            key={`${index}${data}`}
            columns="equal"
            stackable={layout[data].properties.stackable}
            className={
              layout[data].properties.reversedOrder
                ? 'tablet reversed computer reversed'
                : ''
            }
          >
            <GenericWrapper {...layout[data]} />
          </Grid>
        ))}
      </React.Fragment>
    );

    return (
      <React.Fragment>
        <Responsive as={React.Fragment} maxWidth={media.large - 1}>
          {generateGrid(this.state.layout, this.state.layout.mobileOrder)}
        </Responsive>
        <Responsive minWidth={media.large}>
          <Grid columns="equal" padded="vertically">
            <Grid.Row>
              <Grid.Column>
                {generateGrid(
                  this.state.layout,
                  this.state.layout.desktopFullWidthOrder,
                )}
              </Grid.Column>
            </Grid.Row>
            <Grid.Row>
              <Grid.Column className="two-thirds">
                {generateGrid(
                  this.state.layout,
                  this.state.layout.desktopLeftOrder,
                )}
              </Grid.Column>
              <Grid.Column className="third">
                {generateGrid(
                  this.state.layout,
                  this.state.layout.desktopRightOrder,
                )}
              </Grid.Column>
            </Grid.Row>
          </Grid>
        </Responsive>
      </React.Fragment>
    );
  }
}

export default TabsGridContainer;



